.App {
    width: 100vw;
    height: 100vh;
    background: #faf9fb;
    overflow-x: hidden;
  }
  
  .App::-webkit-scrollbar {
    display: none;
  }
  
  /* Header Styles Starts */
  
  header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 15px;
    height: 60px;
    width: 100%;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 101;
    box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.1);
  }
  
  .logo {
    min-width: 40px;
    width: 50px;
  }
  
  .inputBox {
    background: #ffffff;
    border-radius: 25px;
    display: flex;
    align-items: center;
    overflow: hidden;
    padding: 8px 15px;
    width: 120px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  }
  
  .inputBox .searchIcon {
    color: #787a84;
    font-size: 20px;
  }
  
  .inputBox input {
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    background: transparent;
    color: #787a84;
    padding-left: 8px;
    font-size: 14px;
    font-weight: 500;
  }
  
  .shoppingCart {
    position: relative;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .shoppingCart .cart {
    font-size: 30px;
    color: #373848;
  }
  
  .shoppingCart .cart_content {
    width: 20px;
    height: 20px;
    background: #f5ae56;
    border-radius: 100%;
    min-width: 20px;
    min-height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 0;
    top: -8px;
  }
  
  .shoppingCart .cart_content p {
    font-weight: 500;
    color: #faf9fb;
  }
  
  .shoppingCart .noCartItem {
    display: none;
  }
  
  .profileContainer {
    display: flex;
    align-items: center;
  }
  
  .profileContainer .imgBox {
    width: 40px;
    min-width: 40px;
    height: 40px;
    border-radius: 100%;
    background: #444;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  .profileContainer .imgBox img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .profileContainer .userName {
    font-size: 16px;
    margin-left: 8px;
    font-weight: 500;
    display: none;
    
    align-items: center;
  }
  .toggleMenu {
    font-size: 20px;
    padding: 8px;
  }
  .toggleMenu:hover {
    background-color: #ef8b10;
    border-radius: 40%;
  }
  
  /* Header Styles Ends */
  
  /* Bottom Menu Style  */
  
  .bottomMenu{
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60px;
    background: #000000;
    border-radius: 25px 25px 0px 0px;
    z-index: 101;
  }
  
  .bottomMenu ul {
    display: flex;
  }
  
  .bottomMenu ul li {
    position: relative;
    list-style: none;
    width: 60px;
    height: 60px;
    z-index: 1;
  }
  
  .bottomMenu ul li a {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    text-align: center;
    font-weight: 500;
  }
  
  .bottomMenu ul li a .icon {
    position: relative;
    display: block;
    line-height: 70px;
    text-align: center;
    transition: 0.5s;
  }
  
  .bottomMenu ul li a .icon .MuiSvgIcon-root {
    font-size: 26px;
    color: #ffffff;
  }
  
  .bottomMenu ul li.active a .icon {
    transform: translateY(-26px);
  }
  
  .bottomMenu ul li.active a .icon .MuiSvgIcon-root {
    color: #f9f9fb;
    transition: 0.5s;
  }
  
  .indicator {
    position: absolute;
    top: -50%;
    width: 60px;
    height: 60px;
    background: #ef8b10;
    border: 6px solid #faf9fb;
    border-radius: 100vw;
    transition: 0.5s;
  }
  
  .indicator::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -20px;
    width: 18px;
    height: 18px;
    background: transparent;
    border-top-right-radius: 18px;
    box-shadow: 0px -10px 0 0 #faf9fb;
  }
  
  .indicator::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -20px;
    width: 18px;
    height: 18px;
    background: transparent;
    border-top-left-radius: 18px;
    box-shadow: 0px -10px 0 0 #faf9fb;
  }
  
  .bottomMenu ul li:nth-child(1).active ~ .indicator {
    transform: translateX(calc(60px * 0));
  }
  
  .bottomMenu ul li:nth-child(2).active ~ .indicator {
    transform: translateX(calc(60px * 1));
  }
  
  .bottomMenu ul li:nth-child(3).active ~ .indicator {
    transform: translateX(calc(60px * 2));
  }
  
  .bottomMenu ul li:nth-child(4).active ~ .indicator {
    transform: translateX(calc(60px * 3));
  }
  
  .bottomMenu ul li:nth-child(5).active ~ .indicator {
    transform: translateX(calc(60px * 4));
  }
  
  .bottomMenu ul li:nth-child(6).active ~ .indicator {
    transform: translateX(calc(60px * 5));
  }
  /* Main Styles */
  
  main {
    width: 100%;
    height: auto;
    margin-top: 60px;
  }
  
  .mainContainer {
    width: 100%;
    height: auto;
    background: #faf9fb;
    padding: 10px;
    padding-bottom: 100px;
  }
  
  .banner {
    width: 100%;
    height: 600px;
    border-radius: 10px;
    background-color: black;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    align-items: center;
    padding: 0px 10px;
  }
  
  .deliveryPic {
    float: left;
    position: absolute;
    /* right: 60px; */
    top: 0px;
    width: 200px;
    height: 200px;
  }
  
  .bannerContent {
    padding: 8px;
    display: flex;
    align-items:center;
    justify-content: center;
    flex-direction: column;
   
  }
  
  .bannerContent h1 {
    font-size: 60px;
    width: 900px;
    margin-left: 550px;
    font-weight: 900;
    color: #fec700;
    
  }
  .bannerContent h3 {
    font-size: 18px;
    font-weight: 600;
    color: #ffffff;
    margin-left: 80px;
  }
  
  .bannerContent p {
    font-size: 25px;
    width: 900px;
    margin-top: 5px;
    margin-bottom: 8px;
    margin-left: 300px;
    color: #fffdfc;
  }
  
  .bannerContent a {
    background: #f8901c;
    padding: 15px 25px;
    border-radius: 100vw;
    color: #faf9fb;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    margin-left: 80px;
  }
  
  .bannerContent :hover {
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
    transition: 0.5s;
  }
  
  /* dishContaine */
  .dishContainer {
    width: 100%;
    padding: 10px 0;
  }
  
  .menuCard {
    width: 100%;
  }
  
  .subMenuContianer,
  .viewAll {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .subMenuContianer h3 {
    font-size: 16px;
    font-weight: 500;
    color: #2a2b3c;
  }